import TripSortView from './view/trip-event-view.js'; // DAY, EVENT, PRICE
import BoardPresenter from './presenter/board-presenter.js';
import PointsModel from './model/point-model.js';
import FilterView from './view/filter-view.js';
import { render } from './framework/render.js';
import { generateFilters } from './mock/filters-mock.js';

const siteMainElement = document.querySelector('.page-body');
const siteTripInfoElement = siteMainElement.querySelector('.trip-main'); // 
const siteFiltersElement = siteTripInfoElement.querySelector('.trip-controls__filters'); //  EVERYTHING, FUTURE, PAST  .
const siteEventsElement = siteMainElement.querySelector('.trip-events'); //  DAY, EVENT, TIME .,     

const pointsModel = new PointsModel(); //    
const filters = generateFilters(pointsModel.points);
const boardPresenter = new BoardPresenter({
  container: siteEventsElement,
  pointsModel
});

render(new FilterView({filters}), siteFiltersElement);
//render(new TripFilterView(), siteFiltersElement); //  EVERYTHING, FUTURE, PAST  .
render(new TripSortView(), siteEventsElement); //  DAY, EVENT, TIME, PRICE, OFFER

boardPresenter.init(); //   !!!       2= 

